.john_doe_quarter{
    /* float:left;*/
     width:273px; 
     padding:1.5%;
     margin-left:15%;
    /*margin-bottom:1.7cm; */
     box-shadow:1px 1px 1px 1px black;
     cursor:grab; 
     border:1px solid black;
     
}


.john_doe_quarter:hover{
    box-shadow:1px 5px 5px 5px black;
}



// @font-face{
// src:url(custom-fonts/LibreFranklin.ttf);
// font-family:LibreFranklin;
// }


html{
    box-sizing:border-box;
    -ms-text-size-adjust:100%;
    -webkit-text-size-adjust:100%;
    overflow-x:hidden;
}

html,body{
    font-family:Verdana,sans-serif;
    font-size:15px;
    line-height:1.5;
}


body{
    margin:0;
    width: 100% !important;
    margin:0 !important;
    padding:0 !important;
}

button{
    font:inherit;
    margin:0;
}

h1{
    font-size:36px;
}

h2{
    font-size:30px;
}

h3{
    font-size:24px;
}

h4{
    font-size:20px;
}

h5{
    font-size:18px;
}

h6{
    font-size:16px;
}

h1,h2,h3,h4,h5,h6{
    font-family:"Segoe UI",Arial,sans-serif;
    font-weight:400;
    margin:10px 0;
}

table{
    display:block;
    border:1px solid teal;
    border-collapse: collapse;
    width:250px;
    margin-left:auto;
    margin-right:auto;
    font-family: "Fira Sans",sans-serif, "Libre Baskerville",serif; 
}


td {
    border:2px solid teal;
    font-size:12px;
    text-align: center;
    height:2px; 
    width:250px;
    margin-left:auto;
    margin-right:auto; 
}


.john_doe_teal{
    color:#fff!important;
    background-color:#009688!important;
    margin:2px !important;
    width:20%;
    justify-content: space-between;
}



.john_doe_button{
    border:none;
    display:inline-block;
    color:white !important;
    vertical-align:middle;
    overflow:hidden;
    text-decoration:none;
    color:inherit;
    background-color:inherit;
    text-align:center;
    cursor:pointer;
    white-space:nowrap;
}


.john_doe_button:hover{
    color:#000!important;
    background-color:#ccc!important;
}


.john_doe_circle{
    border-radius:3%;
    box-shadow:1px 2px 2px 2px black;
    border:1px solid black;
    height: 175px;
    width:175px; 
    margin-bottom: 4px;
    margin-left:auto;
    margin-right:auto;
}


.john_doe_large{
    font-size:18px!important;
}




